@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
body {
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
.app {
  padding-top: .5rem;
  display: flex;
  justify-content: center;
}

.wrap {
  width: 500px;
}

$height: 46px;

.select {
  width: 100%;
  position: relative;
  z-index: 99;
  &.open {
    .select__dropdown {
      display: block;
    }
    .select__input {
      border-bottom: none;
    }

    .select__back {
      display: block;
    }
  }

  &__input {
    border: 1px solid #ccc;
    border-right: 1px solid #ccc;
    display: flex;
    align-items: center;
    padding: 0 1rem;
    height: $height;
    cursor: pointer;
    justify-content: space-between;
  }
  &__dropdown {
    display: none;
    position: absolute;
    border: 1px solid #ccc;
    border-right: 3px;
    top: $height;
    left: 0;
    right: 0;
    max-height: 200px;
    overflow-y: auto;
    box-shadow: 1px 3px 5px rgb(0,0,0,.3);
  }

  &__list {
    list-style: none;
    padding-left: 0;
    margin: 0;
    li {
      border-bottom: 1px solid #cccccc;
      padding: 1rem;

      &.selected {
        background-color: burlywood;
      }

      &:hover {
        background-color: #eee;
        cursor: pointer;
        transition: background-color .1s;
      }
    }
  }
  &__back {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    display: none;
  }
}